import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Map, MapMarker, MarkerClusterer, useMap } from 'react-kakao-maps-sdk';
import { xmlToJson } from '../util';

var region = {
  "": 11,
  "": 21,
  "": 22,
  "": 23,
  "": 24,
  "": 25,
  "": 26,
  "": 45,
  "": 31,
  "": 32,
  "": 33,
  "": 34,
  "": 35,
  "": 36,
  "": 37, 
  "": 38,
  "": 50
};

const MissionMap = () => {
  const [positions, setPositions] = useState([]);
  const [coo, setCoord] = useState({lat:0, lng: 0});
  const [isOpen, setIsOpen] = useState(false);

  const options = {
    enableHighAccuracy: true,
    maximumAge: 0
  };

  const onSuccess = (pos) => {
    const crd = pos.coords;

    setCoord({lat: crd.latitude, lng: crd.longitude});
  };

  const onError = (err) => {
    console.warn(`ERROR(${err.code}): ${err.message}`);
  };

  const onNildaList = () => {
    axios.get(`https://www.cha.go.kr/cha/SearchKindOpenapiList.do?pageUnit=2000&ccbaKdcd=11`)
    .then((res)=>{
      let XmlNode = new DOMParser().parseFromString(res.data, "text/xml");
      var json = xmlToJson(XmlNode)
      // console.log(json)
      setPositions(...positions, json)
    })
    .catch((err)=>{
      console.log(err)
    });

    axios.get(`https://www.cha.go.kr/cha/SearchKindOpenapiList.do?pageUnit=2000&ccbaKdcd=14`)
    .then((res)=>{
      let XmlNode = new DOMParser().parseFromString(res.data, "text/xml");
      var json = xmlToJson(XmlNode)
      // console.log(json)
      setPositions(...positions, json)
    })
    .catch((err)=>{
      console.log(err)
    });

    axios.get(`https://www.cha.go.kr/cha/SearchKindOpenapiList.do?pageUnit=2000&ccbaKdcd=79`)
    .then((res)=>{
      let XmlNode = new DOMParser().parseFromString(res.data, "text/xml");
      var json = xmlToJson(XmlNode)
      // console.log(json)
      setPositions(...positions, json)
    })
    .catch((err)=>{
      console.log(err)
    });
  }

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(onSuccess, onError, options);
    onNildaList();
  },[]);

  const EventMarkerContainer = ({ position, content }) => {
    const map = useMap()
    const [isVisible, setIsVisible] = useState(false)

    return (
      <MapMarker
        position={position} //   
        // @ts-ignore
        onClick={(marker) => map.panTo(marker.getPosition())}
        onMouseOver={() => setIsVisible(true)}
        onMouseOut={() => setIsVisible(false)}
      >
        {isVisible && content}
      </MapMarker>
    )
  };

  return (
    <section aria-label="" className="p-10">
      <h4 className="text-xl mb-5"> </h4>
      <Map
        center={{ lat: coo.lat, lng: coo.lng }}
        style={{ width: "100%", height: "360px" }}
        level={11}
      >
        <MarkerClusterer
          averageCenter={true}
          minLevel={7}
        >
          {positions.length !== 0 && positions.map((pos, ind) => (
            <EventMarkerContainer 
              key={`${pos.lng}-${pos.lat}-${ind}`}
              position={{lat: pos.lat, lng:pos.lng}}
              content={pos.content}
            />
          ))}
        </MarkerClusterer>
      </Map>
    </section>
  );
};

export default MissionMap;